<script>
export default {
  data() {
    return {
      circles: [1, 2, 3, 4, 5],
      isHovering: "",
      links: [
        {
          link: "tanweishen97@gmail.com",
          img: require("../assets/contact-links/email.svg"),
          altText: "gmail",
        },
        {
          link: "https://github.com/MrMagicPickle",
          img: require("../assets/contact-links/github.svg"),
          altText: "github",
        },
        {
          link: "https://www.linkedin.com/in/wei-shen-tan-21613618a/",
          img: require("../assets/contact-links/linkedin.svg"),
          altText: "linkedin",
        },
        {
          link: "https://leetcode.com/mrmagicpenguin/",
          img: require("../assets/contact-links/leetcode-v2.svg"),
          altText: "leetcode",
        },
      ],
    };
  },
  methods: {
    goTo(link) {
      const a = document.createElement("a");

      if (link === "tanweishen97@gmail.com") {
        a.href = "mailto:" + link;
      } else {
        a.href = link;
      }

      a.target = "_blank";
      a.click();
    },
    highlight(id) {
      this.isHovering = id;
    },
    resetHighlight() {
      this.isHovering = "";
    },
    elColor(id) {
      if (id === this.isHovering) {
        return "#30DEFF";
      }
      return "#c7e4e9";
    },
  },
};
</script>

<style lang="sass" scoped>
#external-links
  position: fixed
  bottom: 0px
  left: 50px
  width: 40px
  display: flex
  flex-direction: column
  justify-content: center

  .link
    width: 30px
    height: 30px
    margin: 10px auto
    cursor: pointer
    transition: all 0.7s ease
    img
      width: 80%
      max-width: 100%
      max-height: 100%

  .link:hover
    transform: translateY(-5px)

  .circles
    width: 7px
    height: 7px
    margin: 10px auto
    background-color: #c7e4e9
    border-radius: 50%
</style>
